<template>
  <el-table :data="productList" class="custom-table">
    <el-table-column
      prop="combineServiceName"
      label="产品名称"
      align="center"
    ></el-table-column>
    <el-table-column
      prop="startTime"
      label="购买日期"
      align="center"
    ></el-table-column>
    <el-table-column
      prop="endTime"
      label="到期时间"
      align="center"
    ></el-table-column>
    <template #empty>
      <TableEmpty empty-text="暂无交易订单"></TableEmpty>
    </template>
  </el-table>
</template>
<script lang="ts" setup>
import { shallowRef } from "vue";

import { useVueRouter } from "@assets/useHook";

import { customerByProductApi } from "@api/customer-management-api";

const { routeQuery } = useVueRouter();
const productList = shallowRef<unknown[]>([]);
// 获取已购产品列表
const getProductList = async () => {
  try {
    const params = {
      companyId: routeQuery.value.companyId,
    };
    const { data } = await customerByProductApi.getData(params);
    productList.value = customerByProductApi.handleData(data);
  } catch (e) {
    console.log(e);
  }
};
getProductList();
</script>
